<template>
  <div>
    <mu-form ref="form" :model="validateForm" class="projectform">
      <mu-form-item
        label=""
        help-text="10"
        prop="projectname"
        :rules="projectnameRules"
      >
        <mu-text-field
          id="name"
          :color="this.$store.state.global.theme.color"
          v-model="validateForm.projectname"
          prop="projectname"
        ></mu-text-field>
      </mu-form-item>
      <mu-form-item label="" prop="projectstart" :rules="startRules">
        <mu-date-input
          id="start"
          :color="this.$store.state.global.theme.color"
          v-model="validateForm.projectstart"
          full-width
          prop="projectstart"
          no-display
          container="bottomSheet"
        ></mu-date-input>
      </mu-form-item>
      <mu-form-item label="" prop="projectend" :rules="endRules">
        <mu-date-input
          id="end"
          :color="this.$store.state.global.theme.color"
          v-model="validateForm.projectend"
          full-width
          prop="projectend"
          no-display
          container="bottomSheet"
        ></mu-date-input>
      </mu-form-item>
      <mu-form-item class="button_box">
        <mu-button :color="this.$store.state.global.theme.color" @click="submit"></mu-button>
        <mu-button :to="{name:'project'}"></mu-button>
      </mu-form-item>
    </mu-form>
  </div>
</template>
<script>
import { pushData, randomId } from "@/utils/data.js";
import getProject from "@/api/projects";
import globalCss from "@/assets/css/global.css";
export default {
  props: ["list"],
  mounted: function() {
    this.$emit("getMessage", this.show);
  },
  created() {
    var projects = localStorage.projects;
    console.log(projects);
    $("body,html").animate({ scrollTop: 0 }, 100);
  },
  data() {
    return {
      show: "project",
      openAlert: false,
      projectnameRules: [
        { validate: val => !!val, message: "" },
        {
          validate: val => val.length < 11,
          message: ""
        }
      ],
      startRules: [{ validate: val => !!val, message: "" }],
      endRules: [
        { validate: val => !!val, message: "" },
        {
          validate: val => Date.parse(val) >= Date.parse($("#start").val()),
          message: "~"
        }
      ],
      validateForm: {
        projectname: "",
        projectstart: "",
        projectend: ""
      }
    };
  },
  methods: {
    submit() {
      this.$refs.form.validate().then(result => {
        console.log(55555);
        if (result) {
          const project = {};
          project.id = randomId();
          project.name = this.validateForm.projectname.trim();
          project.start_at = this.validateForm.projectstart.toLocaleDateString();
          project.end_at = this.validateForm.projectend.toLocaleDateString();
          console.log(project);
          // pushData("projects", project);
          getProject.updateProject(project).then(response => {
            console.log(response);
          });
          console.log(localStorage.projects);
          // this.$router.push({ name: "project" });
        }
      });
    },
    openAlertDialog() {
      this.openAlert = true;
    },
    closeAlertDialog() {
      this.openAlert = false;
    }
  }
};
</script>
