<template>
  <div>
    <mu-form ref="form" :model="validateForm" class="newplanmain">
      <mu-form-item
        label=""
        help-text="10"
        prop="planname"
        :rules="plannameRules"
      >
        <mu-text-field :color="this.$store.state.global.theme.color" v-model="validateForm.planname" prop="planname"></mu-text-field>
      </mu-form-item>

      <mu-form-item
        label=""
        help-text="10010"
        prop="plantotal"
        :rules="plantotalRules"
      >
        <mu-text-field
          :color="this.$store.state.global.theme.color"
          type="number"
          v-model="validateForm.plantotal"
          prop="plantotal"
        ></mu-text-field>
      </mu-form-item>
      <mu-form-item
        label=""
        help-text="5"
        prop="planunit"
        :rules="planunitRules"
      >
        <mu-text-field :color="this.$store.state.global.theme.color" v-model="validateForm.planunit" prop="planunit"></mu-text-field>
      </mu-form-item>
      <!-- <mu-form-item label="" prop="end_at" :rules="endAtRules">
        <mu-date-input
          id="end"
          color="pink200"
          v-model="validateForm.end_at"
          full-width
          prop="end_at"
          no-display
          container="bottomSheet"
        ></mu-date-input>
        </mu-form-item> -->
      <mu-form-item label="" prop="planlevel" class="radio">
        <mu-flex class="select-control-row" v-for="(level,index) in levels " :key="index">
          <mu-radio
            :color="this.$store.state.global.theme.color"
            :value="level.value"
            v-model="validateForm.planlevel"
            :label="level.name"
            class="radios"
          ></mu-radio>
        </mu-flex>
      </mu-form-item>
      <mu-form-item label="" prop="plantype"  class="radio">
        <mu-flex class="select-control-row" v-for="(type,index) in types " :key="index">
          <mu-radio
            :color="this.$store.state.global.theme.color"
            :value="type.value"
            v-model="validateForm.plantype"
            :label="type.name"
            class="radios"
          ></mu-radio>
        </mu-flex>
      </mu-form-item>
      <mu-form-item class="button_box">
        <!-- TODO  -->
        <mu-button :color="this.$store.state.global.theme.color" @click="submit" ></mu-button>
        <mu-button :to="{name:'home'}" ></mu-button>
      </mu-form-item>
      <mu-dialog
        :title="''+validateForm.per+validateForm.planunit"
        width="600"
        max-width="80%"
        :esc-press-close="false"
        :overlay-close="false"
        :open.sync="openAlert"
      >
        {{remain_days}}
        <mu-button slot="actions" flat color="primary" @click="closeAlertDialog"></mu-button>
        <mu-button slot="actions" flat color="primary" @click="createPlan"></mu-button>
      </mu-dialog>
    </mu-form>
  </div>
</template>
<script>
import { remainDays, pushData, randomId, today } from "@/utils/data.js";
import getProject from'@/api/projects'
import getPlan from '@/api/plans'
export default {
  mounted: function() {
    this.$emit("getMessage", this.show);
  },
  created() {
    
    $("body,html").animate({ scrollTop: 0 }, 100);
    const pid = this.$route.query.pid
    getProject.getProjects(pid).then(response=>{
      console.log(response.data)
      this.validateForm.end_at = response.data.end_at
      this.validateForm.pid = pid
    })
  },
  data() {
    return {
      ver: "",
      remain_days: "",
      now: "",
      list: [],
      show: "project",
      openAlert: false,
      types: [
        { name: "7~", value: 7 },
        { name: "6~", value: 6 },
        { name: "5~", value: 5 },
        { name: "1", value: 1 }
      ],
      levels: [
        { name: "", value: 1 },
        { name: "", value: 2 },
        { name: "", value: 3 },
        { name: "", value: 4 }
      ],
      plannameRules: [
        { validate: val => !!val, message: "" },
        {
          validate: val => val.length <= 10,
          message: ""
        }
      ],
      plantotalRules: [
        { validate: val => !!val, message: "" },
        {
          validate: val => val.length <= 10,
          message: ""
        }
      ],
      planunitRules: [
        { validate: val => !!val, message: "" },
        {
          validate: val => val.length <= 5,
          message: "~"
        }
      ],
      // endAtRules:[
      //   { validate: val => !!val, message: "" },
      // ],
      validateForm: {
        // 
        id:randomId(),
        planname: "",
        plantotal: "",
        per:'',
        planunit: "",
        plantype: 7,
        pid: 0,
        planver: 0,
        planlevel: 1,
        created_at:'',
        end_at:'',
        status:0,
        done:0
      }
    };
  },
  watch:{
    validateForm:{
      deep:true,
      handler:function(newItems, oldItems){ 
        console.log(newItems.planlevel) 
    }}
  },
  methods: {
    freshData() {},
    openFullscreenDialog() {
      this.openFullscreen = true;
    },
    openAlertDialog() {
      this.openAlert = true;
    },
    closeAlertDialog() {
      this.openAlert = false;
    },
    createPlan() {
      console.log(this.validateForm)
      getPlan.updatePlan(this.validateForm).then(response=>{
        this.$router.push({ name: "project" });
      })
    },
    submit() {
      this.$refs.form.validate().then(result => {
        this.validateForm.created_at = today()
        this.remain_days = remainDays(this.validateForm.end_at)?remainDays(this.validateForm.end_at):1
        if (result) {
          // this.validateForm.per
          this.validateForm.per = (this.validateForm.plantotal/this.remain_days).toFixed(2)
          this.openAlertDialog();
        }
      });
    }
  },
};
</script>
